.BuildControls {
  width: auto;
  background: -webkit-gradient(linear, left top, right top, from(#ce493798), to(#a5302198));
  background: -webkit-linear-gradient(left, #ce493798, #a5302198);
  background: -o-linear-gradient(left, #ce493798, #a5302198);
  background: linear-gradient(to right, #ce493798, #a5302198);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-align: center;
      align-items: center;
  -webkit-box-shadow: 0 2px 1px #ccc;
          box-shadow: 0 2px 1px #ccc;
  margin: 0 10px;
  padding: 10px 0;
}
.Controls {
  display: -ms-flexbox;
  display: flex;
  padding: 10px 10px;
}
.BuildControls h2 {
  margin: 2px 2px;
  text-align: center;
}
.OrderButton {
  background-color: #a53816;
  outline: none;
  cursor: pointer;
  border: 1px solid #960920;
  color: #ad7d24;
  font-family: inherit;
  font-size: 1.2em;
  padding: 15px 30px;
  -webkit-box-shadow: 2px 2px 2px #973812;
          box-shadow: 2px 2px 2px #973812;
}

.OrderButton:hover,
.OrderButton:active {
  background-color: #4b0d05;
  border: 1px solid #ac0404;
  color: #140803;
}

.OrderButton:disabled {
  background-color: #c7c6c6;
  cursor: not-allowed;
  border: 1px solid #ccc;
  color: #888888;
}

.OrderButton:not(:disabled) {
  -webkit-animation: enable 0.3s linear;
          animation: enable 0.3s linear;
}

@media (max-width: 500px) {
  .Controls {
    display: block;
  }
}

@-webkit-keyframes enable {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  60% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes enable {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  60% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}



